
.starter-template {
    padding: 3rem 1.5rem;
    text-align: center;
  }
  
  img {
    padding: 1rem 1.5 rem;
  }
  
  .btn-primary {
    color: #f0d8b6; 
    background: #9D4019;
    border-color: #9D4019;
  }

  .how-text {
      margin-top: 3em;
  }

 .form-signin {
    width: 100%;
    max-width: 330px;
    padding: 15px;
    margin: 0 auto;
    padding-top: 40px;
    padding-bottom: 40px;
  }
  .form-signin input[type="email"] {
    margin-bottom: 1em;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;

  }
  .form-signin input[type="password"] {
    margin-bottom: 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }

  h4 {
    font-weight: 400;
    font-size: 1em;
    margin-top: 2em;
  }